@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
	font-family: 'GTWalshiem';
	src: url('./assets/fonts/GTWalsheimPro-Medium.ttf') format('truetype');
	font-weight: 500;
}

ul.custom-list {
	list-style: none;
}

ul.custom-list li::before {
	content: '-';
	margin-right: 5px;
}

.fade-in {
	-webkit-animation: fade-in 0.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: fade-in 0.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

.fade-out {
	-webkit-animation: fade-out 0.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: fade-out 0.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

/**
 * ----------------------------------------
 * animation fade-in
 * ----------------------------------------
 */
 @-webkit-keyframes fade-in {
	0% {
	  opacity: 0;
	}
	100% {
	  opacity: 1;
	}
  }
  @keyframes fade-in {
	0% {
	  opacity: 0;
	}
	100% {
	  opacity: 1;
	}
  }

/**
 * ----------------------------------------
 * animation fade-out
 * ----------------------------------------
 */
 @-webkit-keyframes fade-out {
	0% {
	  opacity: 1;
	}
	100% {
	  opacity: 0;
	}
  }
  @keyframes fade-out {
	0% {
	  opacity: 1;
	}
	100% {
	  opacity: 0;
	}
  }
  